import React, { Fragment, useEffect, useState } from 'react';
import axios from 'axios';
import { Map, Marker, Popup, TileLayer, Polyline } from 'react-leaflet';
import { divIcon } from 'leaflet';

import differenceInDays from 'date-fns/difference_in_days';

import { apiUrl } from '../config';
import { isFutureTrip, isPastTrip, isCurrentTrip } from '../helpers/dateUtils';

import Spinner from './Spinner.react';

// Custom Icon for the leaftlet map.
const locationIcon = divIcon({
  html: '<span class="icono-caretDown"/>',
  iconSize: [25, 25],
  iconAnchor: [17, 24],
  popupAnchor: [-3, -26]
});

const LocationMap = ({ userId }) => {
  const [locations, setLocations] = useState(null);

  const fetchUserLocations = id => {
    if (!id) {
      return;
    }
    setLocations(null);
    return axios(`${apiUrl}/u/${id}/locations/`, {
      withCredentials: true
    }).then(res => {
      if (res.status === 200) {
        setLocations(res.data.data);
      } else if (res.status === 404) {
        setLocations([]);
      }
    });
  };

  useEffect(
    () => {
      fetchUserLocations(userId);
    },
    [userId]
  );

  if (locations === null) {
    return <Spinner />;
  }

  // Sort so the soonest trip is first in the list
  locations.sort((a, b) => differenceInDays(a.start_date, b.start_date));

  const pLocations = locations.filter(isPastTrip);
  const fLocations = locations.filter(isFutureTrip);
  const cLocation =
    locations.find(isCurrentTrip) ||
    fLocations[0] ||
    pLocations[pLocations.length - 1];

  const position = [51.505, -0.09];

  return (
    <Fragment>
      <Map center={position} zoom={1} style={{ height: 300 }}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        />
        <Polyline
          positions={pLocations
            .concat([cLocation])
            .map(({ city }) => [city.latitude, city.longitude])}
          color="black"
        />
        <Polyline
          positions={fLocations.map(({ city }) => [
            city.latitude,
            city.longitude
          ])}
          color="#bf4e4e"
        />
        {locations.map(({ id, city, start_date, end_date }) => (
          <Marker
            key={id}
            position={[city.latitude, city.longitude]}
            icon={locationIcon}
          >
            <Popup>{city.name}</Popup>
          </Marker>
        ))}
      </Map>
      <dl className="uk-description-list" style={{ textAlign: 'left' }}>
        <dt>Past Trips</dt>
        <dt style={{ color: '#bf4e4e' }}>Future Trips</dt>
      </dl>
    </Fragment>
  );
};

export default LocationMap;
