import React, { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { fetchCities } from '../actions';

import addDays from 'date-fns/add_days';

import Select from 'react-select';
import Flatpickr from 'react-flatpickr';

import Spinner from '../components/Spinner.react';

const Explore = ({ cities, fetchCities, activeUser }) => {
  const [continent, setContinent] = useState(null);
  const [dates, setDates] = useState([new Date(), addDays(new Date(), 7)]);

  useEffect(() => {
    if (!cities.isFetching && cities.ids.length === 0) {
      fetchCities();
    }
  }, []);

  useEffect(
    () => {
      // Need to implement backend route for this still
      console.log('refetch');
    },
    [continent, dates[0], dates[1]]
  );

  if (cities.isFetching) {
    return <Spinner />;
  }

  const continents = [
    'Europe',
    'North America',
    'South America',
    'Africa',
    'Asia',
    'Oceania'
  ].map(c => ({ value: c, label: c }));

  return (
    <Fragment>
      <h4>FILTERS</h4>
      <div className="uk-flex uk-flex-around">
        <Select
          styles={{
            container: () => ({
              width: 250
            }),
            menu: provided => ({
              ...provided,
              top: null
            })
          }}
          placeholder="Continent"
          className="contient-picker"
          isSearchAble
          isLoading={cities.isFetching}
          value={continent}
          onChange={setContinent}
          options={continents}
        />
        <Flatpickr
          className="uk-input uk-width-1-3@m"
          options={{
            mode: 'range',
            dateFormat: 'Y-m-d'
          }}
          value={dates}
          onChange={setDates}
        />
      </div>
      <h4>SUGGESTIONS</h4>
      <p>Under construction. Sorry!</p>
    </Fragment>
  );
};

const mapStateToProps = ({ cities, activeUser }) => ({
  cities,
  activeUser
});

export default connect(
  mapStateToProps,
  {
    fetchCities
  }
)(Explore);
