import React from 'react';

const FriendshipStatus = ({ user, onRequest, onAccept }) => {
  if (user && user.is_you) {
    return (
      <button className="uk-button uk-flex icon-sm" disabled>
        <span className="icono-smile" />
        <div>You</div>
      </button>
    );
  }
  if (user && user.is_friend) {
    return (
      <button className="uk-button uk-flex icon-sm" disabled>
        <span className="icono-check" />
        <div>Friends</div>
      </button>
    );
  }
  if (user && user.is_pending_friend) {
    return (
      <button onClick={onAccept} className="uk-button uk-flex icon-sm">
        <div>Accept</div>
      </button>
    );
  }
  if (user && user.have_requested_friend) {
    return (
      <button className="uk-button uk-flex icon-sm" disabled>
        <span className="icono-clock" />
        <div>Waiting</div>
      </button>
    );
  }

  return (
    <button className="uk-button uk-flex icon-sm" onClick={onRequest}>
      <span className="icono-plus" />
      <div>Request</div>
    </button>
  );
};

export default FriendshipStatus;
