import React from 'react';

import getMonth from 'date-fns/get_month';
import getDate from 'date-fns/get_date';
import differenceInDays from 'date-fns/difference_in_days';

const MONTHS = [
  'JAN',
  'FEB',
  'MAR',
  'APR',
  'MAY',
  'JUN',
  'JUL',
  'AUG',
  'SEP',
  'OCT',
  'NOV',
  'DEC'
];

const DateRange = ({ start_date, end_date }) => (
  <div className="uk-flex date-range">
    <div>
      <b>
        {getDate(start_date)} <br /> {MONTHS[getMonth(start_date)]}
      </b>
    </div>
    <div>
      <b>
        {getDate(end_date)} <br /> {MONTHS[getMonth(end_date)]}
      </b>
    </div>
    <p
      style={{
        width: 92,
        height: 42,
        fontSize: 16,
        textAlign: 'center',
        margin: 2,
        marginTop: 10
      }}
    >
      {differenceInDays(end_date, start_date)} Day Trip
    </p>
  </div>
);

export default DateRange;
